.input {
  background-color: white;
  border: none;
  border-bottom: 2px solid rgb(38, 62, 94);
  font-size: 20px;
  padding: 3px;
  width: 400px;
  height: 50px;
  color: #092dab;
  margin-top: 120px;
}
.input:focus {
  outline: none;
  border-bottom: 2px solid black;
}

.superInput {
  margin-right: 10px;
}

.errorInput {
  border: 2px solid red;
}

